import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

export async function mapAndFetchMedical(id, setIsloading) {
  // setIsloading(true)
  try {
    const { data: { data } } = await AffiliationEncoderService.getMedicalClinicById(id)
    console.log('response data', data)
    // setIsloading(false)

    return mapData(data)
  } catch (error) {
    console.log('res error get medical', error)
    // setIsloading(false)
  }
}

function mapData(res) {
  const {
    philhealth,
    stage,
    affiliation_status,
    status,
    id,
    type,
    name,
    source,
    mno,
    afn_number,
    tvs,
    tin,
    email,
    address,
    remarks,
    treasury,
    affiliation_infos,
    classification,
    process_type,
    clinic_schedule,
    room_number,
    license,
    contacts,
    operation,
    exclusivity
  } = res

  const formOne = {
    name: '',
    email: '',
    years: '',
    months: '',
    dohNumber: '',
    dohLicenseType: '',
    dohLicenseExp: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '',
    tin: ''
  }

  const formTwo = {
    classification: '',
    nonVatable: '',
    tinStatus: '',
    vatTaxRate: '',
    processType: ''
  }

  const formThree = {
    unitFlrBldg: '',
    bldgName: '',
    street: '',
    subdivision: '',
    region: '',
    district: '',
    barangay: '',
    city: '',
    zipCode: '',
    clinicSchedule: '',
    roomNumber: ''
  }

  const formFour = {
    affiliationInfos: [],
    dentalAssociates: []
  }

  const remarksForm = {
    externalRemarks: '',
    internalRemarks: ''
  }

  const affil = {
    affiliationType: '',
    dateReceived: null,
    id: '',
    mno: '',
    source: 'PNM',
    type: 'CLINIC',
    dateSubmitted: null,
    stageName: 'ENCODING',
    status: 'Active',
    affiliationStatus: 'In-Progress'
  }
  affil.dateSubmitted = stage['date_submitted'] ? stage['date_submitted'] : new Date().toISOString()
  affil.stageName = stage['stage_name'] ? stage['stage_name'] : ''
  affil.type = type
  affil.status = status
  affil.source = source
  affil.affiliationStatus = affiliation_status['name'] ? affiliation_status['name'] : ''
  affil.mno = mno
  affil.officeLocation = res['office_location']
  affil.referenceNo = res['reference_no'] ? res['reference_no'] : ''
  affil.id = id

  formOne.name = name
  formOne.email = email
  formOne.philhealthNumber = philhealth['accreditation_number'] ? philhealth['accreditation_number'] : ''
  formOne.philhealthExpiration = philhealth['expiration_date'] ? philhealth['expiration_date'] : ''
  formOne.years = operation['years'] || ''
  formOne.months = operation['months'] || ''
  formOne.dohNumber = license && license['doh'] ? license['doh']['number'] : ''
  formOne.dohLicenseType = license && license['doh'] ? license['doh']['license_type'] : ''
  formOne.dohLicenseExp = license && license['doh'] ? license['doh']['license_exp'] : ''
  formOne.payee = treasury['payee'] ? treasury['payee'] : ''
  formOne._exclusivity = exclusivity ? exclusivity['enabled'] : false
  formOne._exclusivity_clients = exclusivity ? exclusivity['partners'] : []
  formOne.tin = tin

  formThree.unitFlrBldg = address['unit_flr_bldg'] ? address['unit_flr_bldg'] : ''
  formThree.bldgName = address['building_name'] ? address['building_name'] : ''
  formThree.street = address['street'] ? address['street'] : ''
  formThree.barangay = address['barangay'] ? address['barangay'] : ''
  formThree.district = address['district'] ? address['district'] : ''
  formThree.city = address['city'] ? address['city'] : ''
  formThree.region = address['region'] ? address['region'] : ''
  formThree.zipCode = address['zip_code'] ? address['zip_code'] : ''
  formThree.subdivision = address['subdivision'] ? address['subdivision'] : ''
  formThree.clinicSchedule = clinic_schedule
  formThree.roomNumber = room_number

  formTwo.tinStatus = treasury['tin_status'] ? treasury['tin_status'] : ''
  formTwo.nonVatable = treasury['non_vatable'] ? treasury['non_vatable'] : ''
  formTwo.vatTaxRate = treasury['vat'] ? treasury['vat']['tax_rate'] : ''
  formTwo.classification = classification
  formTwo.processType = process_type

  formFour.contacts = contacts || []
  formFour.affiliationInfos = affiliation_infos || []

  remarksForm.internalRemarks = remarks['internal_remarks'] ? remarks['internal_remarks'] : ''
  remarksForm.externalRemarks = remarks['external_remarks'] ? remarks['external_remarks'] : ''

  let formData = {}
  formData.formOne = formOne
  formData.formTwo = formTwo
  formData.formThree = formThree
  formData.formFour = formFour
  formData.remarksForm = remarksForm

  let form = Object.assign({}, formOne, formTwo, formThree, formFour, remarksForm)

  console.log('Medical Affiliation Form', form)

  return { affil, formData, form }
}
